


import Vue from 'vue';
import Tooltip from "./Tooltip.vue";
import { MountingPortal } from 'portal-vue';

export default Vue.extend({
	name: 'Modal',
	props: {
		show: {
			type: Boolean,
			required: true,
		},
		title: {
			type: String,
			required: true,
		},
		subtitle: {
			type: String,
			required: true,
		},
		submitText: {
			type: String,
			default: 'Save',
		},
		submitDisabled: {
			type: Boolean,
			default: false,
		}
	},
	components: {
		Tooltip,
		MountingPortal,
	},
	data() {
		return {};
	},
	watch: {
		show: function(show) {
			if (show) {
				document.body.classList.add('gp-inventory-modal-visible');
			} else {
				document.body.classList.remove('gp-inventory-modal-visible');
			}
		}
	},
	computed: {
	},
	methods: {}
});
